.modals {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: linear-gradient(315deg, $gradiantBg1 0%, $gradiantBg2 74%);
  padding: 6rem;
  border-radius: 5px;
  box-shadow: 0 3rem 5rem $shadowColorM2;
  z-index: 12;
  width: 450px;
  .close-modal {
    position: absolute;
    top: 0.5rem;
    right: 2rem;
    font-size: 4rem;
    cursor: pointer;
    color: $white;
    background: none;
    border: none;
    padding: 0;
    transition: all 0.2s linear;
    &:hover {
      transform: rotate(90deg) scale(1.2);
      color: $mainColor;
    }
  }
  form {
    div {
      label {
        width: 100%;
        input,
        select {
          width: 100%;
          height: 61px;
          border: none;
          margin: 1rem 0;
          border-radius: 5px;
          outline: none;
          caret-color: $mainColor;
        }
        input {
          padding-left: 1.6rem;
          color: $mainColor;
          font-size: 19px;
          &::placeholder {
            color: $mainColor;
            font-size: 19px;
          }
        }
        select {
          cursor: pointer;
          color: $mainColor;
          padding-left: 1.6rem;
          option {
            color: $mainColor;
          }
        }
      }
      .text-end {
        span {
          display: inline-block;
          color: $validMI;
          font-size: 14px;
          margin-right: 0.2rem;
        }
      }
      button {
        width: 100%;
        height: 61px;
        border: none;
        margin: 1rem 0;
        border-radius: 5px;
        outline: none;
        caret-color: $mainColor;
        background-color: $mainColor;
        color: $white;
        font-size: 21px;
        font-family: "Poppins", sans-serif;
        font-weight: 500;
        cursor: pointer;
        transition: all 0.1s linear;
        &:hover {
          background-color: $btnHoverColor;
          transform: translateY(-4px);
        }
        &:active {
          transform: translateY(-2px);
          background-color: $btnActiveColor;
        }
      }
    }
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $overlayBg;
  backdrop-filter: blur(3px);
  z-index: 11;
}

.hidden {
  display: none;
}

@media (max-width: 992px) {
  .modals {
    width: 60%;
  }
}

@media (max-width: 767px) {
  .modals {
    width: 70%;
    form {
      div {
        label {
          input,
          select {
            height: 40px;
            padding-left: 1rem;
            font-size: 12px;
          }
          input {
            &::placeholder {
              font-size: 12px;
            }
          }
        }
        button {
          height: 40px;
          font-size: 18px;
        }
      }
    }
  }
}

@media (max-width: 420px) {
  .modals {
    width: 80%;
  }
}
