.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 3rem;
  h2 {
    margin-bottom: 3rem;
    font-size: 34px;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
  }
  .form {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5rem 1rem;
    background-color: $backgroundColorForm;
    border-radius: 5px;
    .form-text {
      width: 40%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      h2 {
        font-family: "Poppins", sans-serif;
        font-weight: 600;
        font-size: 27px;
        line-height: 40.5px;
        margin-bottom: 2rem;
        color: $black;
      }
      p {
        font-size: 17px;
        line-height: 25.5px;
        color: $textColorGray;
      }
    }
    .form-box {
      width: 50%;
      text-align: right;
      form {
        div {
          label {
            width: 100%;
            input {
              width: 65%;
              height: 61px;
              border: none;
              margin: 1rem;
              border-radius: 5px;
              outline-color: $mainColor;
              padding-left: 1.6rem;
              color: $textColorGray;
              font-size: 19px;
              &::placeholder {
                color: $textColorGray;
                font-size: 19px;
              }
            }
          }
          span {
            display: inline-block;
            color: $red;
            font-size: 14px;
            margin-right: 1rem;
          }
          button {
            width: 65%;
            height: 61px;
            border: none;
            margin: 1rem;
            border-radius: 5px;
            outline-color: $mainColor;
            background-color: $mainColor;
            color: $white;
            font-size: 22px;
            font-family: "Poppins", sans-serif;
            font-weight: 500;
            cursor: pointer;
            transition: all 0.15s linear;
            &:hover {
              background-color: $btnHoverColor;
              transform: translateY(-4px);
            }
            &:active {
              transform: translateY(-2px);
              background-color: $btnActiveColor;
            }
          }
        }
      }
    }
  }
}

.contact-click-style {
  min-height: calc(80vh - 9.5rem);
  margin-bottom: 0;
}

@media screen and (max-width: 767px) {
  .contact {
    h2 {
      text-align: center;
    }
    .form {
      flex-direction: column;
      align-items: center;
      padding: 2rem;
      .form-text {
        width: 90%;
        h2 {
          font-size: 23px;
          line-height: 35px;
          text-align: center;
        }
        p {
          font-size: 16px;
          line-height: 24px;
          text-align: center;
        }
      }
      .form-box {
        width: 90%;
        form {
          div {
            label {
              input {
                width: 100%;
                height: 50px;
              }
            }
            span {
              margin: 0;
            }
            button {
              width: 100%;
              height: 50px;
            }
          }
        }
      }
    }
  }
}
