.news {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

.news-click-style {
  min-height: calc(80vh - 9.5rem);
}

.news h2 {
  margin: 3rem 0;
  font-size: 34px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

.news .news-card:hover .text-box {
  background-color: $white;
  box-shadow: 0 10px 20px $shadowColor;
}

.news .news-card .img-box {
  overflow: hidden;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.news .news-card:hover .img-box img {
  transform: scale(1.08);
}

.news .news-card .img-box img {
  width: 100%;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
  transition: all 0.2s linear;
}

.news .news-card .text-box {
  background-color: $textBoxColor;
  padding: 1.6rem;
  transition: all 0.2s linear;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}

.news .news-card .text-box .date {
  font-size: 12px;
  color: $textColorGray;
}

.news .news-card .text-box h3 {
  font-size: 17px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin: 1rem 0;
}

.news .news-card .text-box .card-text {
  font-size: 14px;
  line-height: 21px;
  margin-bottom: 1rem;
}

.news .news-card .text-box a {
  color: $white;
}

.effect {
  display: inline-block;
  position: relative;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  border-radius: 6px;
  background-color: $mainColor;
  text-transform: capitalize;
  padding: 0.5rem;
  width: 120px;
  text-align: center;
}

.effect.effect-1 {
  transition: all 0.25s linear;
}

.effect.effect-1::before {
  content: "🠖";
  font-size: 25px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 10px;
  top: 0;
  opacity: 0;
  height: 100%;
  width: 48px;
  transition: all 0.25s linear;
}

.effect.effect-1:hover {
  text-indent: -25px;
}

.effect.effect-1:hover::before {
  opacity: 1;
  text-indent: 20px;
}

/* one page news */

.one-page-news {
  margin: 0 auto;
  width: 60%;
  min-height: 70vh;
}

.one-page-news img {
  width: 100%;
  border-radius: 4px;
  margin-top: 2rem;
}

.one-page-news h1,
.one-page-news p {
  margin: 2rem 0;
}

.one-page-news p {
  text-align: justify;
}

/* ortga qaytish */
.effect2 {
  display: inline-block;
  position: relative;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  border-radius: 6px;
  background-color: $mainColor;
  text-transform: capitalize;
  padding: 0.5rem;
  width: 160px;
  color: $white;
  text-align: center;
}

.effect2.effect-2 {
  transition: all 0.25s linear;
}

.effect2.effect-2::before {
  content: "🠔";
  font-size: 25px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 6px;
  top: 0;
  opacity: 0;
  height: 100%;
  width: 48px;
  transition: all 0.25s linear;
}

.effect2.effect-2:hover {
  text-indent: 25px;
  color: $white;
}

.effect2.effect-2:hover::before {
  opacity: 1;
  text-indent: -20px;
}
/* ortga qaytish */

@media screen and (max-width: 767px) {
  .news {
    align-items: center;
    padding: 1.5rem;
  }
  .news .news-card .text-box button a {
    font-size: 13px;
  }
  .one-page-news {
    width: 95%;
  }
  .one-page-news h1 {
    text-align: center;
    font-size: 4rem;
  }
}

@media (max-width: 550px) {
  .one-page-news h1 {
    text-align: center;
    font-size: 3rem;
  }
}
