// fonts
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

// other css librarys
@import url("https://cdnjs.cloudflare.com/ajax/libs/bootstrap/5.0.2/css/bootstrap.min.css");
@import "react-toastify/dist/ReactToastify.css";
@import "aos/dist/aos.css";

// variable color
@import "./color/color.scss";

// other css
@import "./404";
@import "./contact";
@import "./courses";
@import "./footer";
@import "./header";
@import "./hero";
@import "./loader";
@import "./modal";
@import "./news";
@import "./teacher";
@import "./test";

// Global styles
* {
  scrollbar-width: thin;
  scrollbar-color: $mainColor transparent;
  &::selection {
    color: $white;
    background-color: #00b533;
  }
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  body {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    background-color: $white;
    #root {
      overflow-x: hidden;
    }
    &::-webkit-scrollbar {
      width: 1rem;
    }
    &::-webkit-scrollbar-track {
      background: $footerHeaderBackgroundColor;
    }
    &::-webkit-scrollbar-thumb {
      background: $mainColor;
      border-radius: 1rem;
    }
    h1 {
      font-size: 4.2rem;
    }

    h2 {
      font-size: 3.5rem;
    }

    h4,
    h5 {
      font-size: 2.8rem;
    }

    li,
    button,
    input,
    label,
    p {
      font-size: 2rem;
    }

    div {
      font-size: 1.6rem;
    }

    a {
      text-decoration: none;
      color: $black;
      &:hover {
        color: $black;
      }
    }
  }
}

@media (max-width: 360px) {
  html {
    font-size: 45%;
  }
}
