.hero {
  min-height: 90vh;
  background-image: url("../components/Hero/bg.avif");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  .left-box {
    width: 65%;
    padding: 11.5rem;
    background-image: linear-gradient(
      -90deg,
      $heroBgG1 0%,
      $heroBgG2 34.93%,
      $heroBgG3 52.08%,
      $heroBgG4 79.95%,
      $heroBgG5 100%
    );
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    h1 {
      width: 70%;
      font-size: 43px;
      font-family: "Poppins", sans-serif;
      font-weight: 700;
      line-height: 60px;
    }
    p {
      width: 421px;
      font-size: 17px;
      line-height: 26px;
      margin: 2rem 0;
    }
    button {
      margin-top: 1.5rem;
      border: none;
      width: 253px;
      height: 60px;
      background-color: $mainColor;
      color: $white;
      border-radius: 5px;
      cursor: pointer;
      font-size: 22px;
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      transition: all 0.15s linear;
      &:hover {
        background-color: $btnHoverColor;
        transform: translateY(-4px);
      }
      &:active {
        transform: translateY(-2px);
        background-color: $btnActiveColor;
      }
    }
  }
  @media (max-width: 1600px) {
    .left-box {
      padding-left: 10rem;
    }
  }

  @media (max-width: 1400px) {
    .left-box {
      padding-left: 9rem;
    }
  }

  @media (max-width: 1200px) {
    .left-box {
      padding-left: 8rem;
    }
  }

  @media (max-width: 992px) {
    .left-box {
      padding-left: 6.5rem;
    }
    .left-box h1 {
      font-size: 32px;
      line-height: 45px;
    }
    .left-box p {
      width: 350px;
      font-size: 16px;
    }
    .left-box button {
      width: 220px;
      height: 50px;
      font-size: 17px;
    }
  }

  @media (max-width: 767px) {
    .left-box {
      padding-left: 6rem;
    }
    .hero {
      margin: 0;
      min-height: 90vh;
    }
  }

  @media (max-width: 687px) {
    .left-box {
      padding-left: 3rem;
      width: 70%;
    }
    .left-box h1 {
      font-size: 25px;
      line-height: 35px;
    }
    .left-box p {
      width: 250px;
      font-size: 15px;
    }
    .left-box button {
      width: 180px;
      height: 40px;
      font-size: 17px;
    }
  }

  @media (max-width: 420px) {
    .left-box {
      width: 78%;
    }
    .left-box p {
      width: 200px;
      font-size: 14px;
      line-height: 20px;
    }
    .left-box h1 {
      width: 100%;
      font-size: 25px;
      line-height: 35px;
    }
  }
}
