.tests {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  h2 {
    margin-bottom: 3rem;
    font-size: 34px;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
  }
  div {
    .tests-card {
      .img-box {
        overflow: hidden;
        border-top-left-radius: 7px;
        border-top-right-radius: 7px;
        img {
          width: 100%;
          border-top-left-radius: 7px;
          border-top-right-radius: 7px;
          transition: all 0.2s linear;
        }
      }
      .text-box {
        background-color: $textBoxColor;
        padding: 1.6rem;
        transition: all 0.2s linear;
        border-bottom-left-radius: 7px;
        border-bottom-right-radius: 7px;
        .date {
          font-size: 12px;
          color: $textColorGray;
        }
        h3 {
          font-size: 17px;
          font-family: "Poppins", sans-serif;
          font-weight: 600;
          margin: 1rem 0;
        }
        .card-text {
          font-size: 14px;
          line-height: 21px;
          margin-bottom: 1rem;
        }
        a {
          display: inline-block;
          font-size: 16px;
          font-family: "Poppins", sans-serif;
          font-weight: 500;
          padding: 0.5rem;
          background-color: $mainColor;
          color: $white;
          border-radius: 4px;
          transition: all 0.18s linear;
          margin-bottom: 0.5rem;
          &:hover {
            transform: translateY(-4px);
          }
          &:active {
            transform: translateY(-2px);
          }
        }
        .answersTest {
          float: right;
        }
      }
      &:hover .text-box {
        background-color: $white;
        box-shadow: 0 10px 20px $shadowColor;
      }
      &:hover .img-box img {
        transform: scale(1.08);
      }
    }
  }
}

.tests-click-style {
  min-height: calc(80vh - 9.5rem);
}

@media screen and (max-width: 767px) {
  .tests {
    align-items: center;
    padding: 1.5rem;
    div {
      .tests-card {
        .text-box {
          a {
            font-size: 13px;
          }
        }
      }
    }
  }
}
