header {
  position: sticky;
  top: 0;
  background-color: $footerHeaderBackgroundColor;
  z-index: 10;
}

header nav {
  display: flex;
  align-items: center;
  width: 90%;
  margin: auto;
  padding: 2rem;
  min-height: 10vh;
}

header nav #logo {
  flex: 1 1 50rem;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  flex: 1 1 50rem;
  margin: 0;
}

.nav-links li {
  list-style: none;
}

.nav-links li a {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 17px;
  line-height: 26px;
  color: $mainColor;
  box-shadow: inset 0 0 0 0 $mainColor;
  padding: 0 0.4rem;
  margin: 0 -0.25rem;
  border-radius: 2px;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.nav-links li .active-link {
  background-color: $mainColor;
  color: $white;
  border-radius: 2px;
  padding: 0rem 0.4rem;
}

.nav-links li a:hover {
  color: $white;
  box-shadow: inset 200px 0 0 0 $mainColor;
}

@media screen and (max-width: 1024px) {
  .nav-links {
    flex: 1 1 80rem;
  }
}

@media screen and (max-width: 767px) {
  .nav-links {
    display: none;
  }
  .nav-links.actives {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(
      315deg,
      $gradiantBg1 0%,
      $gradiantBg2 74%
    );
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .nav-links li {
    margin-bottom: 2rem;
  }
  .nav-links li a {
    color: $white;
  }

  // burger
  @mixin icon($height, $width) {
    transition-duration: 0.5s;
    position: absolute;
    height: $height;
    width: $width;
    top: 30px;
    background-color: $mainColor;
  }

  @mixin icon-before($height, $width, $top) {
    transition-duration: 0.5s;
    position: absolute;
    width: $width;
    height: $height;
    background-color: $mainColor;
    content: "";
    top: $top;
  }

  @mixin icon-after($height, $width, $top) {
    transition-duration: 0.5s;
    position: absolute;
    width: $width;
    height: $height;
    background-color: $mainColor;
    content: "";
    top: $top;
  }

  .burger {
    position: absolute;
    width: 60px;
    height: 60px;
    right: 8%;
    transition-duration: 0.5s;
    transform: scale(0.8) translateY(-2.8px);

    .icon-left {
      @include icon(6px, 31px);
      left: 0px;

      &:before {
        @include icon-before(6px, 31px, -20px);
      }

      &:after {
        @include icon-after(6px, 31px, 20px);
      }

      &:hover {
        cursor: pointer;
      }
    }

    .icon-right {
      @include icon(6px, 30px);
      left: 30px;

      &:before {
        @include icon-before(6px, 30px, -20px);
      }

      &:after {
        @include icon-after(6px, 30px, 20px);
      }
    }

    &.open {
      .icon-left {
        transition-duration: 0.5s;
        background: transparent;

        &:before {
          transform: rotateZ(45deg) scaleX(1.4) translate(4px, 4px);
        }

        &:after {
          transform: rotateZ(-45deg) scaleX(1.4) translate(4px, -4px);
        }
      }

      .icon-right {
        transition-duration: 0.5s;
        background: transparent;

        &:before {
          transform: rotateZ(-45deg) scaleX(1.4) translate(-4px, 4px);
        }

        &:after {
          transform: rotateZ(45deg) scaleX(1.4) translate(-4px, -4px);
        }
      }
    }
    &:hover {
      cursor: pointer;
    }
  }
}
