footer {
  background-color: $footerHeaderBackgroundColor;
  .block {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    width: 90%;
    margin: auto;
    min-height: 10vh;
    .block-1 {
      width: 30%;
      .box {
        p {
          color: $mainColor;
          font-weight: 500;
          font-size: 18px;
        }
        .messejener {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          a {
            color: $mainColor;
            display: flex;
            align-items: center;
            font-weight: 500;
            &:first-child {
              margin-right: 2rem;
            }
            .title-messejener {
              transition: all 0.2s linear;
              padding: 0 0.5rem 0 0.2rem;
              border-radius: 1rem;
              &:hover {
                background-color: $mainColor;
                color: $white;
              }
            }
          }
          .fa-telegram,
          .fa-facebook {
            font-size: 2.5rem;
            transition: all 0.2s linear;
            &:hover {
              transform: translateY(-2px);
            }
            &:active {
              transform: translateY(0);
            }
          }
        }
      }
    }

    .block-2 {
      width: 40%;
      display: flex;
      .box {
        width: 50%;
        ul {
          li {
            margin: 1.2rem;
            list-style: none;
            a {
              font-family: "Poppins", sans-serif;
              font-weight: 500;
              font-size: 18px;
              color: $mainColor;
              box-shadow: inset 0 0 0 0 $mainColor;
              padding: 0 0.4rem;
              margin: 0 -0.25rem;
              border-radius: 2px;
              transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
              &:hover {
                color: $white;
                box-shadow: inset 200px 0 0 0 $mainColor;
              }
            }
          }
        }
        &:nth-child(2) {
          display: flex;
          justify-content: flex-end;
        }
      }
    }

    .block-3 {
      width: 30%;
      text-align: right;
      padding-right: 1rem;
      .box {
        p {
          a {
            color: $mainColor;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            font-weight: 500;
            span {
              transition: all 0.2s linear;
              padding: 0 0.5rem 0 0.2rem;
              border-radius: 1rem;
              &:hover {
                background-color: $mainColor;
                color: $white;
              }
            }
          }
          .fa-earth-asia,
          .fa-square-phone {
            font-size: 2.5rem;
            transition: all 0.2s linear;
            &:hover {
              transform: translateY(-2px);
            }
            &:active {
              transform: translateY(0);
            }
          }
        }
      }
    }
  }
}

@media (max-width: 1024px) {
  footer {
    .block {
      .block-1 {
        .box {
          p {
            font-size: 14px;
          }
          .messejener {
            a {
              font-size: 1.2rem;
              &:first-child {
                margin-right: 0.8rem;
              }
              .title-messejener {
                padding: 0 0.5rem;
              }
            }
            .fa-telegram,
            .fa-facebook {
              font-size: 2.2rem;
            }
          }
        }
      }
      .block-2 {
        .box {
          ul {
            li {
              a {
                font-size: 16px;
              }
            }
          }
        }
      }
      .block-3 {
        .box {
          margin-top: 2rem;
          p {
            font-size: 15px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  footer {
    .block {
      display: block;
      padding: 1px;
      .block-1 {
        margin: 2rem 0;
        width: 100%;
        text-align: center;
        .box {
          .messejener {
            justify-content: center;
          }
        }
      }
      .block-2 {
        width: 100%;
        display: flex;
        .box {
          &:nth-child(1),
          &:nth-child(2) {
            display: flex;
            justify-content: center;
          }
        }
      }
      .block-3 {
        margin: 2rem 0;
        width: 100%;
        text-align: center;
        .box {
          p {
            a {
              justify-content: center;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 360px) {
  footer {
    .block {
      .block-1 {
        .box {
          .messejener {
            margin-top: 1.5rem;
            a {
              font-size: 2rem;
            }
          }
        }
      }
    }
  }
}
